import React from "react";
import styled from "styled-components";
import about from "../public/about me.png";
import star2 from "../public/star2.png";
import html from "../public/logos/480px-HTML5_logo_and_wordmark.svg.png";
import css from "../public/logos/CSS.3.svg";
import js from "../public/logos/Javascript_badge.svg.png";
import react from "../public/logos/react.svg";
import redux from "../public/logos/redux.svg";
import bootstrap from "../public/logos/bootstrap.png";
import jest from "../public/logos/jest.png";
import webpack from "../public/logos/webpack.png";
import Antd from "../public/logos/AntD.svg";
import apollo from "../public/logos/apollo-graphql-compact.svg";
import graph from "../public/logos/graph.png";
import RESTAPI from "../public/logos/RESTAPI.png";
import nextjs from "../public/logos/nextjs.png";
import node from "../public/logos/node_js.png";
import express from "../public/logos/express.png";
import mongoose from "../public/logos/mongoose.jpg";
import mongoDB from "../public/logos/mongodb.png";
import jwt from "../public/logos/jwt.png";
import ts from "../public/logos/typescript.png";

const Wrapper = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Arial, serif;
  margin-bottom: 3%;
  div.head {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    div.about {
      height: 200px;
      width: 50%;
      @media (max-width: 700px) {
        text-align: center;
        max-height: 100px;
        padding-left: 5%;
      }
      img {
        height: 100%;
        @media (max-width: 700px) {
          width: 280px;
          max-height: 100px;
          transform: rotate(15deg);
        }
      }
    }
    div.star2 {
      height: 200px;
      width: 50%;
      @media (max-width: 700px) {
        display: none;
      }
      img {
        height: 100%;
        float: right;
      }
    }
  }
  div.text {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    color: black;
    h1 {
      font-size: 50px;
      @media (max-width: 700px) {
        font-size: 30px;
        text-align: center;
      }
    }
    div.h1 {
      width: 100%;
      font-size: 50px;
      @media (max-width: 700px) {
        font-size: 30px;
        text-align: center;
      }
    }
    h3 {
      padding-left: 5%;
      font-size: 20px;
      @media (max-width: 700px) {
        font-size: 14px;
        padding-right: 5%;
      }
    }
  }
  div.stack {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    h1 {
      color: black;
      margin: 2% auto;
    }
    div.front {
      display: grid;
      width: 95%;
      grid-template-columns: repeat(auto-fill, minmax(100px, 20%));
      @media (max-width: 700px) {
        grid-template-columns: repeat(3, 1fr);
      }
      div.img {
        max-height: 120px;
        text-align: center;
        a {
          img {
            max-height: 50px;
          }
          h5 {
            font-family: Arial, "Hiragino Sans GB", "Microsoft Yahei",
              "Microsoft Sans Serif", sans-serif;
            height: 20px;
          }
        }
      }
    }
    div.back {
      display: grid;
      width: 95%;
      grid-template-columns: repeat(auto-fill, minmax(100px, 20%));
      text-align: center;
      @media (max-width: 700px) {
        grid-template-columns: repeat(2, 1fr);
      }
      div.img {
        max-height: 120px;

        a {
          img {
            height: 50px;
          }
          h5 {
            font-family: Arial, "Hiragino Sans GB", "Microsoft Yahei",
              "Microsoft Sans Serif", sans-serif;
            height: 20px;
          }
        }
      }
    }
  }
`;
const text = [
  "Create a mobile responsive SPA with React",
  "Use all modern components libraries (e.g Ant Desing,Bootstrap4, Material Design)",
  "Create an architecture with Redux/MobX/GraphQL",
  "Optimize the count of renders on the page",
  "Use module bundlers (e.g Webpack4,Parcel)",
  "Wrap React components with Styled Components",
  "Route on the page with react-router-dom",
  "Make a Backend with Express.js+Mongoose",
  "Write unit tests with Jest",
  "Also, I am open to new offers and opportunities"
];

const renderText = text.map((item, i) => (
  <h3 key={item + i}>
    {i + 1}. {item}
  </h3>
));

const techFront = [
  { href: "https://www.w3.org/html/", img: html, name: "HTML5" },
  { href: "https://www.w3.org/css/", img: css, name: "CSS3" },
  { href: "https://www.w3schools.com/js/", img: js, name: "JavaScript" },
  { href: "https://www.typescriptlang.org/", img: ts, name: "TypeScript" },
  { href: "https://reactjs.org", img: react, name: "React" },
  { href: "https://redux.js.org", img: redux, name: "Redux" },
  { href: "https://ant.design/", img: Antd, name: "Antd" },
  { href: "https://getbootstrap.com", img: bootstrap, name: "Bootstrap" },
  { href: "https://jestjs.io", img: jest, name: "Jest" },
  { href: "https://webpack.js.org", img: webpack, name: "Webpack" },
  {
    href: "https://www.restapitutorial.com/lessons/httpmethods.html",
    img: RESTAPI,
    name: "RESTAPI"
  },
  {
    href: "https://www.apollographql.com/docs/react/v3.0-beta",
    img: apollo,
    name: "Apollo-Client"
  },
  { href: "https://nextjs.org", img: nextjs, name: "NEXT.js" }
];
const techBack = [
  { href: "https://nodejs.org/en/", img: node, name: "Node.js" },
  { href: "hhttp://expressjs.com/", img: express, name: "Express.js" },
  { href: "https://mongoosejs.com", img: mongoose, name: "Mongoose" },
  { href: "https://www.mongodb.com", img: mongoDB, name: "MongoDB" },
  { href: "https://www.jwt.io", img: jwt, name: "JWT" },
  {
    href: "https://www.apollographql.com/docs/apollo-server/",
    img: apollo,
    name: "Apollo-Server"
  },
  { href: "https://graphql.org/", img: graph, name: "GraphQL" }
];

const renderFront = techFront.map(({ href, img, name }) => {
  return (
    <div className="img" key={href + name + img}>
      <a href={href}>
        <img src={img} alt={img} />

        <h5>{name}</h5>
      </a>
    </div>
  );
});
const renderBack = techBack.map(({ href, img, name }) => {
  return (
    <div className="img" key={href + name + img + href}>
      <a href={href}>
        <img src={img} alt={img} />

        <h5>{name}</h5>
      </a>
    </div>
  );
});

const AboutMe = () => {
  return (
    <Wrapper>
      <div className="head" id="ABOUT">
        <div className="about">
          <img src={about} alt="about" />
        </div>
        <div className="star2">
          <img src={star2} alt="star2" />
        </div>
      </div>
      <div className="text">
        <h1>I am a Middle Frontend Developer</h1>
        <div className="h1">
          <h1>I can:</h1>
        </div>

        {renderText}
      </div>
      <div className="stack">
        <h1>My Frontend Stack</h1>
        <div className="front">{renderFront}</div>
        <h1>My Backend Stack</h1>
        <div className="back">{renderBack}</div>
      </div>
    </Wrapper>
  );
};

export default AboutMe;
